@import "variable.scss";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 16px;
  font-family: "SF Pro Display", "SF Pro Icons", "Helvetica Neue", "Helvetica",
    "Arial", sans-serif;
  font-weight: 400;
  line-height: 24px;
  font-style: normal;
  color: $color-text;
}

p,
a,
span {
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
  line-height: inherit;
  font-style: inherit;
  letter-spacing: inherit;
  text-align: inherit;
}

button:hover {
  cursor: pointer;
}

a {
  text-decoration: none;
}

.link-primary {
  color: $color-primary;
}

.link-secondary {
  color: $color-dark;
}

.visually-hidden {
  display: none;
}

