@import "variable.scss";
@import "common.scss";

.link-pop {
  color: $color-link-pop;
}

#main {
  width: 100%;
  margin-top: 44px;
  .homepage-section {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    gap: 10px;
    // padding: 10px 0 0 0;
    padding-top: 10px;
    color: $color-dark;

    &.tv-gallery {
      flex-direction: row;

      .module-content {
        width: 980px;
        height: 520px;
      }
    }
    .module-content {
      // flex-grow: 0;
      // flex-shrink: 0;
      // flex-basis: 100%;
      // flex-basis: 0 0 100%;
      height: 600px;
    }
    .visibilityhidden {
      display: none;
    }

    .dark-theme {
      color: $color-white;
    }

    .wrapper {
      width: 100%;
      height: 100%;
      position: relative;
      &.iphone-15-pro {
        .headline,
        .subhead {
          color: $color-white;
        }
      }
      .unit-text {
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: center;
        padding: 20px 20%;
        gap: 15px;
        box-sizing: border-box;
        text-align: center;
        // max-width: 450px;
        &.bottom {
          justify-content: end;
        }
        .headline {
          font: $fn-headline-primary-lg;
          text-wrap: wrap;
        }
        .subhead {
          font-size: 1.5rem;
        }
        .cta-links {
          display: flex;
          gap: 15px;
        }
      }
      .unit-image {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          width: 100%;
          height: 100%;
          flex-basis: 100%;
          object-fit: cover;
        }
      }
    }
  }

  .tv-gallery {
    .container {
      max-width: 100%;
      .carousel {
        .carousel-inner {
          width: 100%;
          display: flex;
          column-gap: 10px;
          justify-content: center;
          overflow: hidden;
          .carousel-item {
            // width: 980px;
            min-width: 980px;
            max-width: 980px;
            height: 600px;
            box-sizing: border-box;
            opacity: 50%;
            position: relative;
            .background,
            img {
              z-index: 1;
              position: absolute;
              height: 100%;
              // max-width: 980px;
              width: 100%;
              // object-fit: cover;
            }
            &.active {
              opacity: 100%;
              .body {
                display: block;
              }
            }

            .body {
              padding: 30px 50px;
              z-index: 2;
              position: absolute;
              bottom: 0;
              left: 0;
              display: none;
              font-size: 1.5rem;
              width: 100%;
              .info {
                color: $color-white;
                margin-left: 30px;
                display: inline-block;
                .category {
                  font-weight: bold;
                }
              }
              button.action {
                font-size: 1.5rem;
                border: none;
              }
            }
          }
        }
      }
    }
  }

  .fam-gallery {
    .container {
      // width: 100%;
      max-width: 100%;
      .carousel {
        .carousel-slider {
          white-space: nowrap;
          // text-wrap: nowrap;
          overflow: hidden;

          .carousel-inner {
            transform: translateX(-400px);
            .carousel-item {
              width: 400px;
              height: 230px;
              // margin: 0 0;
              position: relative;
              box-sizing: border-box;
              padding: 0;
              display: inline-block;
              transition: all 3s linear;
              // overflow: hidden;
              .background {
                position: absolute;
                height: 100%;
                width: 100%;
                // max-width: 400px;
                // object-fit: cover;
              }

              .body {
                position: relative;
                width: 100%;
                height: 100%;
                .info {
                  z-index: 1;
                  position: absolute;
                  bottom: 0;
                  left: 0;
                  width: 100%;
                  display: flex;
                  justify-content: space-between;
                  padding: 50px 20px 20px 20px;
                  font-size: 1rem;
                  color: $color-white;
                  background-image: linear-gradient(
                    to top,
                    rgba($color-dark, 0.5),
                    rgba(#000, 0)
                  );

                  svg {
                    width: 50px;
                    fill: $color-white;
                  }
                }
                .action {
                  position: relative;
                  z-index: 2;
                  width: 100%;
                  height: 100%;
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  overflow: hidden;

                  button {
                    transform: translateY(100%);
                    opacity: 0;
                    transition: all 0.3s;
                    border: none;
                  }

                  &:hover {
                    background-color: rgba($color-dark, 0.3);
                    button {
                      transform: translateY(0);
                      opacity: 1;
                    }
                  }
                }
              }
            }
          }
        }
        .carousel-control {
          display: flex;
          justify-content: flex-end;
          padding: 10px;
          button {
            width: 30px;
            height: 30px;
            padding: 0;
            border: none;
            background: none;
            * {
              height: 100%;
              width: 100%;
            }
            .icon-pause {
              display: block;
            }
            .icon-play {
              display: none;
            }
            &.play {
              .icon-pause {
                display: none;
              }
              .icon-play {
                display: block;
              }
            }
          }
        }
      }
    }
  }
}

.button-primary {
  padding: 10px 20px;
  border: 1px solid $color-primary;
  color: $color-white;
  border-radius: 50px;
  box-sizing: border-box;
  background-color: $color-primary;
  font-size: 1rem;
  font-size: $fs-cta-link;
  transition: all 0.1s ease-in;
  &:hover {
    color: $color-primary;
    background-color: $color-white;
  }
}

.button-secondary {
  font-size: $fs-cta-link;
  padding: 10px 20px;
  border: 1px solid $color-dark;
  color: $color-dark;
  border-radius: 50px;
  box-sizing: border-box;
  background-color: $color-white;
  font-size: 1rem;
  transition: all 0.1s ease-in;
  &:hover {
    color: $color-white;
    background-color: $color-dark;
  }
}

@media screen and (min-width: 735px) {
  #main .homepage-section.display-wrap {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 10px;
    padding: 10px 0;
    .module-content {
      // flex-grow: 0;
      // flex-shrink: 0;
      // flex-basis: calc(50% - 15px);
      flex: 0 0 calc(50% - 15px);
      height: 600px;
    }
  }
}

@media screen and (max-width: 1068px) {
  #main {
    .homepage-section {
      .wrapper {
        .unit-text {
          .headline {
            font: $fn-headline-primary-md;
          }
          .subhead {
            font: $fn-subhead-primary-md;
          }
        }
      }
    }

    .tv-gallery {
      .container {
        .carousel {
          .carousel-inner {
            .carousel-item {
              // width: 600px;
              min-width: 600px;
              max-width: 600px;
              height: 400px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 734px) {
  #main {
    .homepage-section {
      .wrapper {
        .unit-text {
          .headline {
            font: $fn-headline-primary-sm;
          }
          .subhead {
            font: $fn-subhead-primary-sm;
          }
        }
      }
    }

    .tv-gallery {
      .container {
        .carousel {
          .carousel-inner {
            .carousel-item {
              // width: 300px;
              min-width: 300px;
              max-width: 300px;
              // height: 600px;
              max-height: 600px;
              .body {
                padding: 10px;
                display: flex !important;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                .info {
                  margin: 10px 0;
                }
              }
            }
          }
        }
      }
    }

    .fam-gallery {
      .container {
        .carousel {
          .carousel-inner {
            transform: translateX(-300px);
            .carousel-item {
              // width: 238px;
              // height: 134px;
              min-width: none;
              max-width: 238px;
              max-height: 134px;
              font-size: 0.75rem;
              .info {
                padding: 5px !important;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (min-width: 1024px) {
}

.container {
  width: 100%;
  max-width: 1024px;
  margin-left: auto;
  margin-right: auto;
  box-sizing: border-box;
}
